.category {
  margin-bottom: 27px;

  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: -18px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}

.category__wrapper {
  padding: 0;
}

.category__title {
  margin: 0 auto;
  padding-top: 71px;
  padding-right: 30px;
  padding-bottom: 121px;
  padding-left: 30px;
  max-width: 375px;
  min-height: 282px;

  font-size: 27px;
  font-weight: 700;
  text-align: center;
  color: #010000;
}

@media (min-width: $tablet-width) {
  .category {
    position: relative;

    margin-bottom: 46px;

    background-image: url("../img/hero-tablet@1x.jpg");
    background-position-y: -60px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 1150px auto;
    }

    &::after {
      @include pseudo;

      bottom: -13px;
      right: 0;

      width: 100%;
      height: 24px;

      background-image: url("../img/svg/bg-zigzag-fill.svg");
      background-size: contain;
    }
  }

  .category__wrapper {
    padding: 0 50px;
  }

  .category__title {
    padding-top: 94px;
    padding-bottom: 373px;
    max-width: 668px;
    min-height: 567px;

    font-size: 43px;
    line-height: 50px;
    color: #000000;
  }
}

@media (min-width: $desktop-width) {
  .category {
    margin-bottom: 125px;

    background-image: url("../img/hero-desktop@1x.jpg");
    background-position-y: -58px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }

    &::after {
      bottom: -18px;

      height: 34px;

      background-size: auto;
    }
  }

  .category__title {
    padding-top: 96px;
    padding-bottom: 359px;
    max-width: none;
    min-height: 0;

    font-size: 53px;
    line-height: 57px;
  }
}
