.user-block__item {
  border-bottom: 1px solid #ececec;
}

.user-block__wrapper {
  display: flex;
}

.user-block__link {
  position: relative;

  display: block;
  padding-top: 26px;
  padding-right: 56px;
  padding-bottom: 28px;
  padding-left: 75px;

  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;

  &::before {
    @include svg-ie;

    z-index: 1;
  }

  &[href]:hover,
  &[href]:focus {
    color: rgba(#000000, 0.6);
  }

  &[href]:active {
    color: rgba(#000000, 0.3);
  }

  &[href]:hover .user-block__icon,
  &[href]:focus .user-block__icon {
    fill: #63d1bb;
  }

  &[href]:active .user-block__icon {
    fill: rgba(#63d1bb, 0.3);
  }
}

.user-block__icon {
  position: absolute;

  fill: rgba(#231f20, 0.2);

  &--search {
    top: 31px;
    left: 32px;
  }

  &--cart {
    top: 29px;
    left: 30px;
  }
}

@media (min-width: $tablet-width) {
  .user-block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .user-block__item {
    border: none;

    &--decor {
      border-right: 1px solid rgba(68, 68, 68, 0.1);
      border-left: 1px solid rgba(68, 68, 68, 0.1);
    }
  }

  .user-block__link {
    &--search {
      padding-top: 36px;
      padding-right: 32px;
      padding-bottom: 22px;
      padding-left: 32px;

      font-size: 0;
    }

    &--cart {
      padding-top: 31px;
      padding-right: 0;
      padding-bottom: 33px;
      padding-left: 83px;
    }
  }

  .user-block__icon {
    &--search {
      position: relative;

      top: 0;
      left: 0;
    }

    &--cart {
      top: 34px;
      left: 31px;
    }
  }
}

@media (min-width: $desktop-width) {
  .user-block__link {
    &--cart {
      padding-bottom: 33px;
    }
  }
}
